body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f8ff;
  font-family: 'Arial', sans-serif;
}

.container {
  text-align: center;
  overflow: hidden;
}

.apology-message {
  font-size: 3rem;
  color: #ff4500;
}

.panda-image {
  position: absolute;
  width: 200px;
  left: -200px;
}

.panda-hang{
  position: absolute;
  top: 0;
  right: 0;
  width: 150px;
}